.pad {
  display: inline-block;
  vertical-align: bottom;
  width: 80px;
  height: 80px;
  margin: 5px;
  background-color: #AAA;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.pad:active {
  background-color: #AFA;
}


